Table {
  font-size: 14px;
}

.form-input-tag-bottom-space {
  margin-bottom: 10px;
  font-weight: 600;
}

.input-tag-style {
  background-color: #f8fafc;
  font-weight: 700;
}

.hide-scrollbar {
  overflow: auto; /* Content is scrollable */
  scrollbar-width: thin; /* Reduces the scrollbar size in Firefox */
}

.hide-scrollbar::-webkit-scrollbar {
  width: 0px; /* Sets scrollbar width for Chrome, Safari, and Edge */
}
.ant-select-selector {
  background-color: #f8fafc !important;
}
