  /* Remove padding from table header cells */
.custom-table-row .ant-table th {
  padding: 8px !important; /* Removes padding from table header cells */
}

/* Remove padding from table data cells */
.custom-table-row .ant-table td {
  padding: 2px !important;
  max-height: 125px !important; /* Removes padding from table data cells */
}



